*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 10px; }

body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 1.6rem;
    color: $color-grey;
    letter-spacing: .2rem;
    padding: 5rem;
    background-color: white; }

.container {
    background-color: #F7F7F7; }
